				margin: 0;
				text-align: center;
			}

			a {
				&.stretched-link {
					color: $gris;
					font-size: 20px;
					text-transform: uppercase;
					font-weight: bold;
					display: flex;
					align-items: center;
					text-decoration: none;
					letter-spacing: 1.5px;
					@include mobile {
						font-family: Varsity;
						font-weight: normal;
						color: white;
						font-size: 38px;
						justify-content: center;
						flex-direction: column;
					}

					span {
						font-size: 1rem;
						font-weight: normal;
						margin-left: 1rem;
						text-transform: lowercase;
						@include mobile {
							display: none;
							margin-left: 0;
						}
					}
				}

				&.btn {
					margin-top: 2rem;
					font-size: 13px;
				}
			}
		}

		.imagen {
			position: relative;
			@include mobile {
				&::before {
					content: '';
					position: absolute;
					top: 0;
					bottom: 0;
					right: 0;
					left: 0;
					background-color: rgba(0, 0, 0, .5);
				}
			}
		}

		.texto {
			@include mobile {
				position: absolute;
			}
		}

		&:hover {
			p {
				a {
					color: $rojo;
				}
			}
		}
	}
}

