		position: relative;
		height: 270px;
		@include mobile {
			height: 270px;
		}

		.onda-negra {
			position: absolute;
			bottom: -1px;
			right: -1px;

			img {
				height: 240px;
				@include mobile {
					height: 150px;
				}
			}
		}

		.box-podcast-wrapper {
			position: absolute;
			top: 0;
			right: 3rem;
			width: 670px;
			@include mobile {
				right: 0;
			}
		}

		.box-podcast--escucha {
			margin-bottom: 1.5rem;
			float: left;
			@include mobile {
				margin-bottom: .75rem;
				margin-right: 1rem;
			}

			img {
				width: 350px;
				@include mobile {
					width: 250px;
				}
			}
		}

		.box-podcast--flecha {
			float: left;
			margin: 3rem 0 0 -4rem;
			transform: rotate(-15deg);
			@include mobile {
				padding-left: 2rem;
				margin-bottom: 0;
			}

			img {
				width: 70px;
			}
		}

		.box-podcast--icono {
			display: flex;
			justify-content: flex-end;
			margin-right: 1rem;
			margin-top: 4.5rem;

			img {
				width: 120px;
				@include mobile {
					width: 80px;
				}
			}

			a {
				margin-top: 1.2rem;
				display: block;
				text-align: center;
				color: white;
				font-weight: bold;
				font-size: 1.5rem;
				letter-spacing: 2px;
				text-decoration: none;
				@include mobile {
					font-size: 1.1rem;
				}

				&:hover {
					color: $gris;
				}
			}
		}
	}

	.logo {
		@include mobile {
			text-align: center;
			margin: 2rem 0;
		}

		img {
			width: 200px;
		}
	}

	ul.footer-links {
		@include mobile {
			text-align: center;
			margin-bottom: 0;
		}

		li {
			margin: 5px 0;
			@include mobile {
				margin: 10px 0;
			}

			a {
				color: white;
				@include mobile {
					font-size: 1.2rem;
				}
			}
		}
	}

	ul.footer-logos {
		@include mobile {
			margin: 2rem 0 1.5rem;
		}

		li {
			margin-right: 10px;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	ul.footer-social {
		margin-bottom: 1rem;

		li {
			margin-right: 10px;
			@include mobile {
				margin-right: 1.5rem;
			}

			&:last-child {
				margin-right: 0;
			}

			a {
				color: white;

				i {
					font-size: 1.6rem;
				}

				&:hover {
					color: $gris;
				}
			}
		}
	}

	.logos-wrapper {
		@include mobile {
			display: flex;
			flex-direction: column-reverse;
			ul {
				justify-content: center;
			}
		}
	}
}

